<template>
  <div class="fims">
      <Loop cName="films-loop" v-if="datalist.length>0">
        <div class="swiper-slide" v-for="data in datalist" :key="data.bannerId">
          <img :src="data.imgUrl" alt="">
        </div>
      </Loop>
      <div class="films-nav">
        <ul>
          <router-link active-class="active" v-for="data in msg" :key="data.id" tag="li" :to="data.path">{{data.title}}</router-link>
        </ul>
      </div>
      <router-view></router-view>
  </div>
</template>

<script>
import Loop from '@/components/Loop'
import Swiper from 'swiper'
import {instance} from '@/utils/http'
import {mapState} from 'vuex'
export default {
  data(){
    return{
      datalist:[],
      msg:[
        {id:1,title:'正在热映',path:'nowplaying'},
        {id:2,title:'即将上映',path:'comingSoon'}
      ]
    }
  },
  components:{
    Loop
  },
  computed:{
    ...mapState("city",["cityId"])
  },
  created(){
  instance(`/gateway?type=2&cityId=${this.cityId}&k=5575458`,{
    headers:{
      'X-Host': 'mall.cfg.common-banner'
    }
  }).then(res=>{
    if(!this.datalist){
      this.datalist = [...res.data.data,{imgUrl:'https://static.maizuo.com/v5/upload/189bcf606b4bf49ad5de201a2ea5024d.jpg',bannerId:666}]
    }
    this.$nextTick(()=>{
      new Swiper('.films-loop',{
        loop:true,
        pagination:{
          el:'.swiper-pagination'
        }
      })
    })
  })
  }
}

</script>
<style lang="scss" scoped>
.fims{
  width: 100%;
  height: 192px;
  .films-nav{
    width: 100%;
    height: .4rem;
    ul{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      li{
        padding: 5px;
        border-bottom: 1px solid transparent;
        &.active{
          color: orange;
        border-bottom: 1px solid orange;
        }
      }
    }
  }
  img{
    width: 100%;
    height: 100%;
  }
}
</style>